type Locale = 'pt-br';

export function currency(value: string | number, locale: Locale) {
  switch (locale) {
    case 'pt-br':
      return currencyPtBr(value);
    default:
      return value;
  }
}

function currencyPtBr(value: string | number) {
  if (typeof value === 'string') {
    return `R$ ${Number(value).toFixed(2).replace('.', ',')}`;
  }

  if (typeof value === 'number') {
    return `R$ ${value.toFixed(2).replace('.', ',')}`;
  }
}
